@import "../../../sass/consts";

.goods_types_page {
  height: 100%;
  background-color: $bg1;
  align-items: flex-start !important;
  overflow-y: hidden;

  > div:not(.home_img) {
    height: 100%;
    overflow-y: scroll;
  }

  .types {
    background-color: white;
    font-size: .8rem;
    > div {
      padding: .5rem 0 .5rem 1.5rem;
      width: 7rem;
      position: relative;
      line-height: 2rem;

      &.active {
        color: $main1;
        background-color: $bg1;

        &:after {
          content: '';
          position: absolute;
          left: .5rem;
          top: .5rem;
          display: inline;
          height: 2rem;
          width: .2rem;
          background-color: $main1;
        }
      }
    }
  }

  .sub_types {
    margin: .5rem;
    background-color: white;
    border-radius: .5rem;
    height: calc(100% - 1rem);
    font-size: 0;
    padding: 1rem 0;
    overflow-y: auto;

    .sub_type {
      display: inline-block;
      vertical-align: baseline;
      width: 30%;
      margin-top: 1rem;

      text-align: center;

      &.center{
        margin: 1rem 5% 0;
      }

      img {
        width: 100%;
      }

      div {
        font-size: .5rem;
        padding-top: 1rem;
      }
    }
  }
}
